import React from "react";

function FinalScoreCard(props)
{
    return (<>{props.scoreBoard.map((scores)=>{
        return <div class="scoreCard">
            <h2 id="playerName">{scores.name}</h2>
            <h2 id="playerPoints">{scores.score}</h2>
        </div>
    })}
    </>);
}

export default FinalScoreCard;