import {useState} from "react";
import { FontAwesomeIcon } from "@fortawesome/react-fontawesome";
import ClockModal from "./ClockModal.js";

function Player(props)
{
    const url = "https://picsum.photos/id/"+(200+props.index)+"/100";
    const [currentPoints,setCurrentPoint] = useState(props.score);
    const [showModal,setShow] = useState(false);

    function handleShowModal()
    {
        setShow(true);
    }
    function handleHideModal()
    {
        setShow(false);
    }
    function handleIncrement()
    {
        let incrementValue = Number(prompt('Enter the value by which to increment?'));
        incrementValue = incrementValue === 0?1:incrementValue;
        setCurrentPoint(currentPoints+incrementValue);
        props.onCallback(props.name,currentPoints+incrementValue);
    }

    function handleDecrement()
    {
        let decrementValue = Number(prompt('Enter the value by which to decrement?'));
        decrementValue= decrementValue === 0?1:decrementValue;
        setCurrentPoint(currentPoints-decrementValue);
        props.onCallback(props.name,currentPoints-decrementValue);
    }

    function handleReset()
    {
        setCurrentPoint(0);
        props.onCallback(props.name,0);
    }

    return <><div id="playerInfo">
            <h2 id="playerName"><img src={url} alt="random profile" className="profileImg"/> {props.name} <div className="iconClock" onClick={handleShowModal}><FontAwesomeIcon icon={['far', 'clock']} size="lg"/></div></h2>
        <h2 id="playerPoints">{currentPoints}</h2>
        <div className="clickButtons">
            <button onClick={handleIncrement} className="btn btn-primary btn-points" >Increment</button>
            <button onClick={handleDecrement} className="btn btn-primary btn-points">Decrement</button>
            <button onClick={handleReset} className="btn btn-primary btn-points">Reset</button>
            <div className="iconTrash" onClick={()=>props.onDelete(props.name)}>
                <FontAwesomeIcon icon={['far', 'trash-alt']} size="lg"/>
            </div>
        </div>
    </div><ClockModal onShow={showModal} onCallback={handleHideModal} name={props.name}/></>;
}
export default Player;