import React,{useState} from "react";
import { Modal, Button} from "react-bootstrap";

function ClockModal(props)
{
  let currentTimeOfUser = 0;
  let pastTimeOfUser = "";
  let pastTimeStorage = JSON.parse(window.localStorage.getItem("pastTime"));
  let currentTimeStorage = JSON.parse(window.localStorage.getItem("currentTimeUser"));
  if(pastTimeStorage === null || pastTimeStorage === undefined)
      pastTimeStorage = {};
  else
      pastTimeOfUser = pastTimeStorage[props.name]!==null && pastTimeStorage[props.name]!==undefined?pastTimeStorage[props.name]:"";
  if(currentTimeStorage === null || currentTimeStorage === undefined)
      currentTimeStorage = {};
  else
      currentTimeOfUser = currentTimeStorage[props.name]!== null && currentTimeStorage[props.name]!==undefined?currentTimeStorage[props.name]:0;
  const [isActive, setIsActive] = useState(false);
  const [isPaused, setIsPaused] = useState(true);
  const [time, setTime] = useState(currentTimeOfUser);
  const [pastTime,setPastTimer] = useState(pastTimeOfUser);
  
  React.useEffect(() => {
    let interval = null;
  
    if (isActive && isPaused === false) {
      interval = setInterval(() => {
        setTime((time) => time + 1000);
        currentTimeStorage[props.name]=time+1000;
        window.localStorage.setItem("currentTimeUser",JSON.stringify(currentTimeStorage));
      }, 1000);
    } else {
      clearInterval(interval);
    }
    return () => {
      clearInterval(interval);
    };
  }, [isActive, isPaused,time,props.name,currentTimeStorage]);
    
    const handleStart = () => {
      setIsActive(true);
      setIsPaused(false);
    };
    
    const handlePauseResume = () => {
      setIsPaused(!isPaused);
    };
    
    const handleReset = () => {
      setIsActive(false);
      setTime(0);
      currentTimeStorage[props.name]=0;
      window.localStorage.setItem("currentTimeUser",JSON.stringify(currentTimeStorage));
    };
    
    function saveTime()
    {
      let pTime = ("0" + Math.floor((time / 60000) % 60)).slice(-2)+":"+("0" + Math.floor((time / 1000) % 60)).slice(-2);
      setPastTimer(pTime);
      pastTimeStorage[props.name]=pTime;
      window.localStorage.setItem("pastTime",JSON.stringify(pastTimeStorage));
    }

    return (<Modal show={props.onShow} onHide={props.onCallback} backdrop="static" keyboard={false} style={{opacity:1}} animation={false}>
        <Modal.Header closeButton>
        </Modal.Header>
        <Modal.Body>
        <div className="timer">
        <h1>
        <strong>
            <span className="digits">
              {("0" + Math.floor((time / 60000) % 60)).slice(-2)}:
            </span>
            <span className="digits">
              {("0" + Math.floor((time / 1000) % 60)).slice(-2)}
            </span>
            </strong>
            </h1>
        </div>
        {!isActive?<div className="btn btn-primary btn-start" onClick={handleStart}>Start</div>
        :(<div className="btn-grp">
          <div className="btn btn-primary btn-one" 
              onClick={handleReset}>
            Reset
          </div>
          <div className="btn btn-primary btn-two" 
              onClick={handlePauseResume}>
            {isPaused ? "Resume" : "Pause"}
          </div>
        </div>)}
        </Modal.Body>
        <Modal.Footer>
        <span>
         {pastTime !== "" && <p><strong>{pastTime}</strong></p>}
         <Button variant="secondary" className="btn-danger btn-time" onClick={saveTime}>Save Time</Button>
          <Button variant="secondary" onClick={props.onCallback} className="btn-danger">Close</Button></span>
        </Modal.Footer>
      </Modal>);
}

export default ClockModal;