import './App.css';
import {useState} from "react";
import Player from "./components/Player";
import FinalScoreCard from './components/FinalScoreCard';
import SearchResult from './components/SearchResult';

function App() {
  let currentStorage = JSON.parse(window.localStorage.getItem('gameScores'));
  if(currentStorage===null)
    currentStorage=[];
  else
  {
    let cloneCurrentStorage=[];
    for(let i=0;i<currentStorage.length;i++)
    {
      if(currentStorage[i]!==undefined && currentStorage[i]!==null)
        cloneCurrentStorage.push(currentStorage[i]);
    }
    currentStorage=cloneCurrentStorage;
  }
  const [playerName,setPlayerName] = useState('');
  const [query,setQuery] = useState('');
  const [mapPlayerScore,setPlayerScore] = useState(currentStorage);
  const [shouldDisplayResult,setShouldDisplayResult] = useState(false);
  const [board,setBoard] = useState([]);
  const [filterPlayer,setFilterPlayer] = useState([]);
  const [shouldDisplaySearchResults,setShouldDisplayResultSearchResults] = useState(false);

  function handleChange(event)
  {
    setPlayerName(event.target.value);
  }

  function handleClick()
  {
    let cloneMapPlayerScore = [...mapPlayerScore];
    cloneMapPlayerScore.push({name: playerName,score: 0});
    setPlayerScore([...mapPlayerScore,{name: playerName,score: 0}]);
    window.localStorage.setItem('gameScores',JSON.stringify(cloneMapPlayerScore));
    setPlayerName('');
  }

  function handleCallback(name,point)
  {
    let cloneMapPlayerScore = [...mapPlayerScore];
    for(let i=0;i<cloneMapPlayerScore.length;i++)
    {
      if(cloneMapPlayerScore[i]?.name===name)
      {
        cloneMapPlayerScore[i]={...cloneMapPlayerScore[i],score: point};
        break;
      }
    }
    setPlayerScore(cloneMapPlayerScore);
    window.localStorage.setItem('gameScores',JSON.stringify(cloneMapPlayerScore));
  }

  function createScoreSheet()
  {
    setShouldDisplayResult(true);
    let scoreCard = [];
    for(let i = 0;i<mapPlayerScore.length;i++)
    {
      if(mapPlayerScore[i]!==null && mapPlayerScore[i]!==undefined)
        scoreCard.push(mapPlayerScore[i]);
    }
    scoreCard.sort((a,b)=>a?.score>b?.score?1:-1);
    scoreCard.reverse();
    alert(`Congratulations!! ${scoreCard[0].name}`)
    setBoard(scoreCard);
  }

  function handleDelete(name)
  {
    let cloneMapPlayerScore = [...mapPlayerScore];
    let cloneFilterPlayer = [...filterPlayer];
    for(let i=0;i<cloneFilterPlayer.length;i++)
    {
      if(cloneFilterPlayer[i]?.name === name)
      {
        delete cloneFilterPlayer[i];
        break;
      }
    }
    for(let i=0;i<cloneMapPlayerScore.length;i++)
    {
      if(cloneMapPlayerScore[i]?.name === name)
      {
        delete cloneMapPlayerScore[i];
        break;
      }
    }
    setFilterPlayer(cloneFilterPlayer);
    setPlayerScore(cloneMapPlayerScore);
    window.localStorage.setItem('gameScores',JSON.stringify(cloneMapPlayerScore));
  }

  function resetScoreboard()
  {
    setShouldDisplayResult(false);
    setPlayerScore([]);
    window.localStorage.setItem('gameScores',JSON.stringify([]));
    setShouldDisplayResultSearchResults(false);
  }

  function handleSearch()
  {
    if(shouldDisplaySearchResults === false)
    {
      let createFilterPlayers = [];
      for(let i = 0;i<mapPlayerScore.length;i++)
      {
        if(mapPlayerScore[i] && mapPlayerScore[i]?.name.substring(0,query.length)===query)
          createFilterPlayers.push(mapPlayerScore[i]);
      }
      setFilterPlayer(createFilterPlayers);
      setShouldDisplayResultSearchResults(true);
    }
    else
    {
      setQuery('');
      setFilterPlayer([]);
      setShouldDisplayResultSearchResults(false);
    }
  }

  function handleQueryChange(event)
  {
    setQuery(event.target.value);
  }

  function checkIfAllUndefined()
  {
    let count=0;
    for(let i=0;i<mapPlayerScore.length;i++)
    { 
      if(mapPlayerScore[i]===undefined || mapPlayerScore[i]===null)
        count++;
    }
    return (count===mapPlayerScore.length);
  }

  return (
    <div className="App">
      <div className="addPlayer">
        <label><span><h3>Player: </h3></span></label>
        <input type="text" className="player-name" name="playerName" value={playerName} placeholder="Player Name" onChange={handleChange}/>
        <button onClick={handleClick} className="btn btn-primary btn-add-player">Add Player</button>
      </div>
      {!checkIfAllUndefined() && <div className="addPlayer">
        <label><span><h3>Search: </h3></span></label>
        <input type="text" className="player-name" value={query} name="playerQueried" onChange={handleQueryChange}/>
        <button className="btn btn-primary btn-search-player" onClick={handleSearch}>{!shouldDisplaySearchResults?"Search Player":"Cancel Search"}</button>
      </div>}
      {!shouldDisplayResult && !shouldDisplaySearchResults && mapPlayerScore.map((mapPlayer,index) => {
        return (mapPlayer!==undefined && mapPlayer!==null && <Player index={index} name={mapPlayer?.name} score={mapPlayer?.score} onCallback={handleCallback} onDelete={handleDelete}></Player>)
      })
      }
      {
      !shouldDisplayResult && !shouldDisplaySearchResults && !checkIfAllUndefined() && <button className="btn btn-success createBoard" onClick={createScoreSheet}>Create Score Sheet</button>
      }
      {
        shouldDisplayResult && !shouldDisplaySearchResults && <FinalScoreCard scoreBoard={board}/>
      }
      {
        shouldDisplayResult && !shouldDisplaySearchResults && <button class="btn btn-info createBoard" onClick={resetScoreboard}>Reset Scoreboard</button>
      }
      {
        shouldDisplaySearchResults && <SearchResult currentPlayerList={filterPlayer} onSearchCallback={handleCallback} onSearchDelete={handleDelete}/>
      }
    </div>
  );
}

export default App;
