import Player from './Player';
function SearchResult(props)
{
    return (<div>{props.currentPlayerList.map((player,idx) => {
        return (player !== undefined) && <Player index={idx} name={player.name} score={Number(player.score)} onCallback={props.onSearchCallback} onDelete={props.onSearchDelete}></Player>
      })}</div>);
}

export default SearchResult;